import React from 'react'
import { Link } from 'gatsby'
import '../components/Layout.css'

const Layout = ({children}) => {
    return (
        <>
            <nav>
                <div class="topnav">
                <Link to='/'>Home</Link>              
                <Link to='/page1'>Page1</Link>
                <Link to='/page2'>Page2</Link>
                </div>
            </nav>
            <main>
                <div class="content">
                {children}
                </div>
                <div class="footer">
                 <p>Demo Website for Baron co management dashboard. all content are subjected to change. Not for Public Use, all Rights Reserved.</p>
                </div>
            </main>
        </>
    )
}
export default Layout